.input {
    flex: 1.5;
    background-color: white;
    border-radius: 0 0 1rem 0;
    width: 100%;

    margin: 0 .5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    .input-field {
        border: none;
        outline: none;
        color: #2f2d52;
        font-size: 1rem;
        height: 80%;
        width: 80%;

        &::placeholder {
            color: var(--placeholder-color);
        }
    }

    .send {
        border: none;
        margin-right:1rem;
        height: 2rem;
        width: 4rem;
        border-radius: 5px;
    }
}

 /* For mobile phones */
 @media (max-width: 480px) {

    .input {
        border-radius: 0;
    }
 }